














































































import Vue from 'vue';
import { FormModel } from '@/models/FormModel';

const MODAL_WIDTH = 656;
export default Vue.extend({
  data() {
    return {
      modalWidth: MODAL_WIDTH,
      repeatFormList: [] as FormModel[],
    };
  },
  computed: {
  },
  created() {
    this.modalWidth = window.innerWidth < MODAL_WIDTH ? MODAL_WIDTH / 2 : MODAL_WIDTH;
  },
  methods: {
    init() {
      this.repeatFormList = this.$store.state.repeatFormList;
    },
    applyMemberInfo(memberForm: FormModel) {
      this.$store.commit('updateMemberForm', memberForm); // 取用此member資料
      this.$store.commit('updateConfirmApplication', true); // 標示接受套用
      this.$store.commit('updateCheckedRepeatList', true); // 標示檢查過了
      this.$modal.hide('check-info-repeat-modal');
    },
    createMemberInfo() {
      this.$store.commit('updateCheckedRepeatList', true); // 檢查過了
      this.$modal.hide('check-info-repeat-modal');
      this.$router.push({ name: 'Longevity' });
    },
  },
});
